<template>
  <div id="app">
    <div class="ui fixed inverted menu vue-color">
      <div class="ui container">
        <a href="#" class="header item">Vue JS CRUD with Laravel API | xsukmo</a>
      </div>
    </div>


     <!-- panggil data produk -->
    <div class="ui main container">
      <MyForm :form="form" @onFormSubmit="onFormSubmit"/>
      <Loader v-if="loader"/>
      <ProdukList :produks="produks" @onDelete="onDelete" @onEdit="onEdit"/>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import MyForm from "../components/MyForm";
import ProdukList from "../components/ProdukList";
import Loader from "../components/Loader";
import Swal from "sweetalert2";

export default {

  name: "App",
  components: {
    MyForm,
    ProdukList,
    Loader
  },
  data(){
    return{
      url:"https://bl.ajisukmo.tech/api/produk",
      urldelete:"https://bl.ajisukmo.tech/api/delete",
      urlpost:"https://bl.ajisukmo.tech/api/tambahproduk",
      urledit:"https://bl.ajisukmo.tech/api/editproduk",
          
      produks:[],
      form:{ nama_produk:'', harga:'',kondisi:'', isEdit:false },
      loader:false
    }
  },
  methods:{

        // ambil data produk
        getproduks(){
          this.loader = true;
          axios
          .get(this.url)
          .then(data => {this.produks = data.data.data; 

          this.loader = false;
          });
      },  

      // hapus data produk
      deleteProduk(id){
          this.loader= true;
          axios 
          .delete(`${this.urldelete}/${id}`)
                .then(()=> { this.getproduks(); })
                .catch(e => {alert(e);
                }); 
            },

       // simpan data produk baru
          createProduk(data){
          this.loader= true;

          axios 
          .post(this.urlpost, {
            nama_produk: data.nama_produk,
            harga: data.harga,
            kondisi: data.kondisi
          })
          .then(()=> this.getproduks())
          .catch(e =>  alert(e))
          Swal.fire({
          icon: "success",
          title: "Produk Berhasil Ditambahkan!"
        });
            },

          // edit data produk
          editProduk(data) {
            this.loader = true;
            axios
              .put(`${this.urledit}/${data.id}`, {
                nama_produk: data.nama_produk,
                harga: data.harga,
                kondisi: data.kondisi
              })
              .then(()=> { this.getproduks(); })
              .catch(e => {
                alert(e);
              });
               Swal.fire({
          icon: "success",
          title: "Produk Berhasil Diupdate!"
        });
          },

          // jalankan fungsi
            onDelete(id) { 
              Swal.fire({
              title: 'Apakah Anda Yakin?',
              text: "Ingin menghapus data id: " + id,
              icon: 'warning',
              showCancelButton: true,
              confirmButtonColor: 'red',
              cancelButtonColor: 'primary',
              confirmButtonText: 'Delete'
            }).then((result) => {
              if (result.value) {
                this.deleteProduk(id),
                Swal.fire(
                  'Deleted!',
                  'Data Berhasil Di hapus.',
                  'success'
                )
              }
            })
 
                        },
          onEdit(data){
                  // window.console.log("edit jon", data);
                  
                  this.form = data;
                  this.form.isEdit = true;
              },
          onFormSubmit(data){
                  // window.console.log("app onformSubmit", data)
          
          if(data.isEdit){
            this.editProduk(data)
          } else {
            this.createProduk(data)
          }
        }
        },
        created(){
          this.getproduks();
        },
        
      };
</script>

<style>
.vue-color {
  background: #009C95 !important;
}
.main.container {
  margin-top: 60px;
}
.submit-button {
  margin-top: 24px !important;
  float: right;
}
.data {
  margin-top: 15px;
}
thead tr th {
  background: #eee !important;
}
.ui.inverted.dimmer {
  background-color: rgba(255, 255, 255, 0) !important;
}
</style>