<template>
<div class="container">
	<div class="box">
            <form class="ui form">
            <div class="field">
                <label>Nama Produk</label>
                <input type="text" placeholder="First Name" v-model="produk.nama_produk">
            </div>
            <div class="field">
                <label>Harga</label>
                <input type="text"  placeholder="Last Name" v-model="produk.harga">
            </div>
                 <div class="six wide field">
          <label>Kondisi Produk</label>
        <select class="ui fluid dropdown"  name="kondisi" v-model="produk.kondisi">
      <option value="baru">Baru</option>
     <option value="bekas">Bekas</option>
      </select>
        </div>
            <button @click="postProduk()" class="ui button teal fluid" type="submit"> <i class="save icon"></i> Simpan</button>
            </form>
            </div>
            </div>
</template>

<script>
import Axios from 'axios';

export default {
            data(){
                return{
                    produk: {
                        nama_produk: "",
                        harga:"",
                        kondisi:""
                    }
                }
            },
            methods:{
            postProduk(){

            let dataproduk = {
                nama_produk: this.produk.nama_produk,
                harga: this.produk.harga,
                kondisi: this.produk.kondisi
            };
            Axios
            .post("https://bl.ajisukmo.tech/api/tambahproduk", dataproduk)
            .then(res => { console.log(res) })
            .catch(err => console.log(err))
            }
            }
            }
</script>

<style>
.box {
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top:10%;
}

.box div {
  width:100%;
  height: 100%;
}
</style>
